import * as DbManagerGrpc from "../../assets/proto/dataImport/db_manager_grpc_web_pb"
import {Pagination} from "../../assets/proto/dataImport/common/pagination_pb"

let client = new DbManagerGrpc.DBManagerPromiseClient(
    process.env.VUE_APP_HZ_NEWS_URL
);
export default {
    //获取数据库列表
    async listDatabases(pageNo,pageSize){
        let page =  new Pagination();
        console.log("page",page);
        page.setPageNo(pageNo);
        page.setPageSize(pageSize);
        let listDatabasesRequest = new DbManagerGrpc.ListDatabasesRequest();
        listDatabasesRequest.setPagination(page);
        let res = await client.listDatabases(listDatabasesRequest,{});
        return res.toObject().databasesList
    },
    //获取上传文件的数据库id
    async addAccessDataBase(name,file){
        // console.log("DbManagerGrpc",DbManagerGrpc);
        let addAccessDataBaseRequest = new DbManagerGrpc.AddAccessDataBaseRequest();
        addAccessDataBaseRequest.setFilename(name);
        
        //将file.raw转化为Uint8Array格式
        //这是一个异步操作 将reader.onload部分的逻辑封装在一个Promise中，并使用await等待它的完成
        const readPromise = new Promise((resolve) => {
            const reader = new FileReader();
            reader.onload = (event) => {
                const arrayBuffer = event.target.result; // 获取文件的二进制数据
                const uint8Array = new Uint8Array(arrayBuffer); // 将二进制数据转换为 Uint8Array
                addAccessDataBaseRequest.setMdb(uint8Array);
                resolve(); // 完成读取操作，解析 Promise
            };
            reader.readAsArrayBuffer(file.raw); // 读取文件内容并触发 onload 事件
        });
        await readPromise; // 等待读取操作完成 如果没有await则mdb来不及set到request里面
      
        console.log("addAccessDataBaseRequest.toObject()",addAccessDataBaseRequest.toObject())
        let res = await client.addAccessDataBase(addAccessDataBaseRequest,{});
        console.log("mdb res---",res.toObject().id)
        return res.toObject().id   //返回mdbId
    }
}